@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@1,200&family=IBM+Plex+Sans&display=swap);
:root{--background-color: rgb(248, 230, 226);--header-font: 'IBM Plex Mono', monospace;--primary-font: 'IBM Plex Mono', sans;--error-color: red;--succes-color: rgb(41, 209, 41)}*{box-sizing:border-box}body{margin:0;font-family:'IBM Plex Mono', sans;font-family:var(--primary-font);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:rgb(248, 230, 226);background-color:var(--background-color)}.App{height:100vh;display:flex;align-items:center}.content{position:fixed;height:100%;width:100vw;max-height:950px;display:grid;grid-template-columns:20% auto;grid-template-rows:40% 30% 30%;grid-template-areas:'header-nav main' 'header-nav main ' 'footer main'}.error{color:red;color:var(--error-color)}.success{color:var(--success-color)}.header-nav{grid-area:header-nav;display:flex;flex-flow:column nowrap;justify-content:flex-end;align-items:flex-end}nav,header{margin-right:30px}img.camera{position:relative;left:5px;top:10px;padding:2px}img.camera:hover{-webkit-filter:brightness(400%);filter:brightness(400%)}h1{text-align:left;font-size:2rem;margin:0;padding:0}h1 a{font-family:'IBM Plex Mono', monospace;font-family:var(--header-font);color:black;text-decoration:none}a{text-decoration:none;color:black}main{width:100%;grid-area:main;place-self:stretch}nav{grid-area:nav}nav>ul{width:100%}footer{grid-area:footer;display:flex;flex-flow:row nowrap;justify-content:center;grid-gap:10px;gap:10px;place-self:start end;width:100%}footer>a{font-size:1.5rem}footer>a>i{margin:0;padding:0}footer a:hover,nav a:hover{transition:0.2s;opacity:0.5}header.mobile{width:100%;top:0;display:none}header.mobile h1{text-align:center}@media (max-width: 900px){.content{grid-template-columns:200px auto}}@media (max-width: 500px){header{margin:0;display:none;grid-area:unset}header.mobile{visibility:unset;display:unset;grid-area:header}.content{position:relative;max-height:unset;z-index:500;grid-template-columns:auto;grid-template-rows:60px auto 45px;grid-template-areas:'header' 'main' 'footer'}footer{grid-gap:10px;gap:10px;place-self:start center}footer>a{font-size:2rem}}

ul{list-style-type:none;font-size:110%}.projects-list{font-size:85%;padding:2px 15px}li.active>a{font-weight:bolder}.active a:hover{opacity:1}

.header-nav{height:100%;transition:0.3s}.unfold-btn{display:none;position:absolute;background-color:rgba(0,0,0,0.8);top:60px;left:105%;width:50px;height:50px;border:none;border-radius:50%;z-index:15000}.unfold-btn:hover{background-color:rgba(0,0,0,0.3)}@media (max-width: 500px){.header-nav{background-color:rgba(248,230,226,0.8);justify-content:center}.header-nav{position:fixed;z-index:2000;width:80%}.header-nav ul li{font-size:120%}.unfold-btn::before{content:'';position:relative;display:block;width:20px;height:3px;background-color:white;top:-5px;left:7px;transform:rotate(-45deg)}.unfold-btn::after{content:'';position:relative;display:block;width:20px;height:3px;background-color:white;top:5px;left:7px;transform:rotate(45deg)}.folded{transform:translateX(-100%)}.folded .unfold-btn::after{top:5px;left:12px;transform:rotate(-45deg)}.folded .unfold-btn::before{top:-5px;left:12px;transform:rotate(45deg)}.unfold-btn{display:inline;display:initial}}

.gallery-container{width:100%;height:100%;padding-right:20px}.child{width:100%;height:100%;margin:0 0 0 0}.col{display:flex;flex-flow:column nowrap;justify-content:center;grid-gap:25px;gap:25px;height:100%;width:100%}.row{display:flex;flex-flow:row nowrap;height:45%;width:100%;grid-gap:25px;gap:25px}.row img:hover{cursor:pointer}.picture{height:100%}@media (max-width: 850px){.gallery-container{min-height:100%}.col,.row{grid-gap:10px;gap:10px}}

input,textarea{display:block;width:100%;background-color:rgba(255,255,255,0.2);border:1px solid black;font-family:'Courier New', Courier, monospace;border-radius:1px;transition:0.2s}textarea{height:200px}input[type=submit]{background-color:rgba(0,0,0,0.2)}input[type=submit]:hover{background-color:rgba(0,0,0,0.8);color:#fae8e3}.contact{margin:0 20px;height:100%;max-width:100%;display:flex;flex-flow:column nowrap;justify-content:center;align-items:center}form{max-width:500px;display:flex;flex-flow:column nowrap;justify-content:left;grid-gap:8px;gap:8px;overflow:scroll;width:100%}@media (max-width: 500px){.contact{width:100%;align-items:center;margin:0}form{width:85%}}

.About{height:100%;width:100%;display:grid;grid-template-columns:250px 350px;grid-template-rows:40% 60%;grid-template-areas:"article-text portraits" "article-text portraits "}.portraits{display:flex;grid-area:portraits;justify-content:center;flex-flow:column nowrap;grid-area:portraits;align-items:center}@media (max-width: 500px){.About{display:flex;flex-flow:column nowrap;overflow:scroll}}

.article-text{display:flex;justify-content:center;flex-flow:column nowrap;grid-area:article-text;padding:0 15px}.article-text p{text-align:justify}

.Projects{height:100%;width:100%;display:grid;grid-template-columns:30% 70%;grid-template-rows:auto;grid-template-areas:'article-text gallery'}.gallery-container{grid-area:gallery;padding-left:20px}@media (max-width: 850px){.Projects{display:flex;flex-flow:column nowrap;overflow:scroll}.gallery-container{height:80vh}}

.Login{width:100%;display:flex;flex-flow:column nowrap;align-items:center;justify-content:center;margin:0;height:100%}.Login input,.Login button{margin-bottom:1em;height:2.5em;line-height:0.5em;font-size:1em}.Login button{background-color:rgba(0,0,0,0.3);border:none;border-radius:3px;font-weight:bolder}.Login button:hover{background-color:rgba(0,0,0,0.5)}

.ProgressBar{display:grid;grid-template-columns:50px auto;grid-template-rows:auto;grid-template-areas:"symbol message";width:100%;height:100%}.message{grid-area:message;display:flex;align-items:center;justify-content:center}.symbol{grid-area:symbol;display:flex;align-items:center;justify-content:center}.loader{position:relative;top:2px}.fa-check-circle{grid-area:symbol;font-size:40px;margin:0px 5px}

.EditBar{display:grid;grid-template-columns:auto 40px 150px;grid-template-rows:auto;grid-template-areas:'output upload buttons';align-content:center;border-top:2px solid rgba(0,0,0,0.5);width:100%;height:45px;background-color:rgba(0,0,0,0.3)}.EditBar .upload-container,.EditBar .output-container,.EditBar .button-container{height:100%;width:100%;display:flex;align-items:center;justify-content:center}.EditBar .upload-container{grid-area:upload}.EditBar .button-container{grid-area:buttons;display:flex;grid-gap:10px;gap:10px}.EditBar .output-container{grid-area:output}.EditBar .button-group{position:relative;height:35px;width:50px;display:flex;flex-flow:row nowrap;align-items:center;justify-content:center;border:2px solid black;border-left:none;border-right:none;margin:0 15px}.EditBar .button-group .checkbox{display:block}.EditBar .button-group button{border:none}.EditBar .button-group button[disabled]:hover{opacity:unset;cursor:not-allowed}.EditBar .button-group .ctr{position:absolute;top:-2px;left:-3px;width:16px;height:16px;background-color:#0075ff;color:white;font-size:12px;text-align:center;font-weight:bolder;border-radius:50%;display:flex;align-items:center;justify-content:center}.EditBar .invisible{visibility:hidden}.EditBar .drag-buttons{display:flex;width:100%;justify-content:center;grid-gap:25px;gap:25px}.EditBar .button-group::after{box-sizing:border-box;position:absolute;content:'';left:50px;height:35px;width:18px;border:2px solid black;border-bottom-right-radius:25px;border-top-right-radius:25px;border-left:none}.EditBar .button-group::before{box-sizing:border-box;position:absolute;content:'';left:-18px;height:35px;width:18px;border:2px solid black;border-bottom-left-radius:25px;border-top-left-radius:25px;border-right:none}

.UploadButton{padding:5px;display:flex;justify-content:center;align-items:center}form.upload{box-sizing:border-box;display:inline-block;height:35px;width:35px;border-radius:50%;border:2px solid rgba(0,0,0,0.7);background-color:rgba(0,0,0,0);transition:0.2s}form.upload:hover{background-color:rgba(0,0,0,0.3);border:2px solid rgba(0,0,0,0.9)}form.upload::before{content:'';position:relative;top:14px;left:6px;display:block;width:20px;height:4px;background-color:rgba(0,0,0,0.7);border-radius:2px}form.upload::after{content:'';position:relative;top:-33px;left:14px;display:block;width:4px;height:20px;background-color:rgba(0,0,0,0.7);border-radius:2px}form.upload:hover::after,form:hover::before{background-color:rgba(0,0,0,0.9)}input[type=file]{position:relative;top:-3px;display:inline-block;height:35px;width:35px;opacity:0;border-radius:50%;z-index:1000}

.image-list{border:2px solid rgba(0,0,0,0.5);border-radius:2px;margin:0 5%}.image-list ol{margin:0;width:100%;height:500px;overflow:scroll;counter-reset:ctr;list-style:none}.image-list button{background-color:transparent;height:35px;width:35px;border-radius:50%;border:2px solid rgba(0,0,0,0.7);background-color:rgba(0,0,0,0)}.image-list button i{font-size:16px}.image-list button:hover{opacity:0.5}.image-list ol li{counter-increment:ctr;position:relative;height:50px;width:100%;display:grid;grid-template-columns:100px auto 50px;grid-template-rows:auto;grid-template-areas:'img name action'}.image-list ol li img{height:40px;border:3px solid #f0ebe1;background-color:#f0ebe1;box-shadow:3px 2px 4px rgba(0,0,0,0.6)}.image-list ol li .img-select{display:block;margin-right:10px;padding:0;width:20px}.image-list ol li .action-container{grid-area:action}.image-list ol li .img-container{grid-area:img}.image-list ol li .img-name,.image-list ol li .img-container,.image-list ol li .action-container{height:100%;display:flex;align-items:center;justify-content:center}.image-list ol li .img-name{grid-area:name;justify-content:start;white-space:nowrap;overflow:scroll}.image-list ol li .drag-btn{cursor:-webkit-grab;cursor:grab;border:none}.image-list ol li .drag-btn:active{cursor:-webkit-grabbing;cursor:grabbing}.image-list ol li::before{text-align:center;line-height:190%;position:absolute;content:counter(ctr);font-size:1.5rem;font-weight:bold;background:rgba(0,0,0,0.8);height:50px;width:40px;left:-40px}.image-list ol li:nth-child(odd)::before{background-color:rgba(0,0,0,0.1)}.image-list ol li:nth-child(even)::before{background-color:rgba(0,0,0,0.3)}.image-list li:nth-child(odd){background-color:rgba(0,0,0,0.2)}.image-list li:nth-child(even){background-color:rgba(0,0,0,0.4)}.image-list li.selected:nth-child(odd){background-color:rgba(0,0,255,0.2)}.image-list li.selected:nth-child(even){background-color:rgba(0,0,255,0.4)}@media (max-width: 900px){.image-list{margin:0}.image-list ol li{grid-template-columns:70px auto 25px}.image-list ol li .img-select{margin-right:0}}@media (max-width: 500px){.image-list ol{height:50vh}}

.edit-nav{margin:0;display:flex;flex-flow:column nowrap;position:absolute;margin-bottom:50px;z-index:1000;margin-top:-50px;max-width:650px;width:100%}.invisible{display:none}.upper-bar{display:flex;justify-content:flex-end;background-color:rgba(0,0,0,0.5);align-items:center;height:50px;margin:0}.upper-bar h2{margin:0 10px;padding:0}.upper-bar .button-group{display:flex;flex-flow:row nowrap;align-items:center}.upper-bar .button-group button{align-items:center;background-color:transparent;border:none;width:40px;height:40px;margin-right:8px;font-size:1rem;color:rgba(255,245,238,0.883)}.upper-bar .button-group button i{font-size:180%}.upper-bar .button-group button.log-out{background-color:rgba(255,245,238,0.883);width:80px;height:30px;border-radius:2px;font-weight:bolder;color:black}.upper-bar .button-group button:hover.log-out{background-color:rgba(255,245,238,0.5)}.lower-bar{background-color:rgba(0,0,0,0.5);margin:0;line-height:2em}.lower-bar ul>li>a{color:rgba(255,245,238,0.883)}.lower-bar button.add{border-radius:50%;border:2px solid rgba(255,245,238,0.883);background-color:transparent;color:rgba(255,245,238,0.883);font-weight:bolder}

#modal{margin:0;position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.8);z-index:10000;display:flex;justify-content:center;align-items:center}#modal img{background-image:url(/static/media/camera.c4947113.svg);background-size:200px;background-repeat:no-repeat;background-position:center;max-width:60%;max-height:80%;border:5px solid whitesmoke;box-shadow:2px 2px 8px black}#modal form{display:flex;background:white;width:400px;height:150px;padding:30px}#modal form input,#modal form button{height:2em}@media (max-width: 500px){#modal img{max-width:80%;max-height:90%}}

.text-edit{display:flex;flex-flow:colum nowrap;justify-content:center;height:500px}.text-edit input[type='text']{background-color:rgba(255,245,238,0.883);border-radius:3px}.text-edit .save{margin:-35px auto 20px auto;width:300px}.editor{min-height:300px;margin-bottom:50px;border:1px solid black}

.about-uploader{width:90%;margin:auto;border:2px solid black;padding:30px 0;margin-bottom:30px}.img-display{margin:auto;width:300px;display:flex;flex-flow:column nowrap;align-items:center;justify-content:center}.img-display img{width:250px}

.EditMenu{height:100%;margin:50px 0 50px 0;display:flex;flex-flow:column nowrap}.EditMenu h2,.EditMenu h3{margin-left:10px}.EditMenu .delete{background-color:#a33131;color:whitesmoke;border-radius:3px;border:none;align-self:center;margin:-30px 0 50px 0;font-size:1.2em;width:300px}

.Admin{font-weight:bolder;max-width:650px;height:100%;overflow:scroll;display:flex;flex-flow:column nowrap;margin:auto;width:100%}@media (min-width: 500px){.Admin{background-color:seashell;box-shadow:2px 0px 15px grey;border-radius:2px}}

